import $ from 'jquery';
import Prism from 'filter_prismjs/external/prism';

export const init = () => {
  // TODOJH: Remove
  // console.warn('filter_prismjs-events-init.js');
  // alert('filter_prismjs-events-init.js');
  $(document).on("filter-content-updated", function (event, nodes) {
    nodes.each(function() {
      // Use the javascript rep of each node
      let node = $(this)[0];
      try {
        // Check if node is valid flow content node
        node.querySelectorAll(':root');
        Prism.highlightAllUnder(node);
      } catch (e) {
        console.warn('PrismJS: ', node, 'not a valid node');
      }
    });
  });
};
